.franchise-backdrop {
    background-color: var(--backdrop-bcg-color);
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 2000;
    overflow-y: scroll;
    transition: opacity 400ms cubic-bezier(0.4, 0, 0.2, 1),
        visibility 400ms cubic-bezier(0.4, 0, 0.2, 1);

    &.is-hidden {
        opacity: 0;
        visibility: hidden;
        pointer-events: none;
        }
}

.franchise-modal {
    width: 280px;
    height: 674px;
    background-color: var(--primary-text-color);
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border: 2px solid var(--modal-border-color);
    border-radius: 10px;
    padding: 28px;
    overflow-y: scroll;
    font-size: 14px;
}

.franchise-modal__title {
    font-family: 'Seymour One', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 1.5;
    color: var(--secondary-text-color);
    word-spacing: -2px;
    margin-bottom: 20px;
}

.franchise-modal__text {
    font-family: 'Montserrat', sans-serif;
    font-weight: 400;
    line-height: 1.32;
    margin-bottom: 24px;
}

.franchise-modal-form {
    display: flex;
    flex-direction: column;
    gap: 12px;
}

.franchise-modal-form__input {
    width: 100%;
    height: 32px;
    color: var(--secondary-text-color);
    line-height: 1.7;
    background-color: var(--input-bcg-color);
    border-radius: 10px;
    border: 1px solid var(--primary-background-color);
    outline-color: var(--hover-btn);
    transition: background-color var(--timing-function);
    outline-color: var(--hover-btn);
    border-radius: 10px;
    padding-left: 12px;
    padding-right: 12px;
    // transition: border-color 300ms cubic-bezier(0.4, 0, 0.2, 1);

    &:focus {
        outline: none;
        border-color: var(--accent-color);
    }
    
    &:not(:focus):not(:placeholder-shown):valid {
        background-color: var(--input-true-bcg);
    }
    
    &:not(:focus):not(:placeholder-shown):invalid {
        background-color: var(--input-false-bcg);
    }

    &::placeholder {
        font-size: 12px;
    }

    &.error {
        border-color: red;
    }
}

.franchise-modal-form__textarea {
    width: 100%;
    height: 64px;
    padding: 8px 12px;
    resize: none;
    background-color: rgba(248, 210, 78, 0.1);
    border: 1px solid #84a178;
    border-radius: 10px;
    transition: border-color 300ms cubic-bezier(0.4, 0, 0.2, 1);
    margin-bottom: 8px;

    &::placeholder {
        font-size: 12px;
    }

    &:focus {
        border-color: var(--hover-btn);
        outline: none;
    }
}

.franchise-modal-form__btn {
    cursor: pointer;
    display: block;
    margin: auto;
    background-color: transparent;
    padding: 0;
    background-color: var(--modal-border-color);
    border-radius: 10px;
    width: 114px;
    height: 42px;
    font-weight: 600;
    font-size: 12px;
    line-height: 1.25;
    border: none;
    color: var(--secondary-text-color);
    transition: background-color 300ms cubic-bezier(0.4, 0, 0.2, 1),
        color 300ms cubic-bezier(0.4, 0, 0.2, 1);

    &:hover {
        background-color: var(--hover-btn);
        color: var(--secondary-text-color);
    }

    &:focus {
        background-color: var(--hover-btn);
        color: var(--primary-text-color);
    }
}

.modal-btn-close {
    cursor: pointer;
    background-color: transparent;
    padding: 0;
    position: absolute;
    top: 6px;
    right: 6px;
    border: none;
}

@media screen and (min-width: 768px) {
    .franchise-modal {
        width: 568px;
        height: 668px;
        padding: 32px 50px;
        display: flex;
        flex-direction: column;
        overflow-y: auto;
    }

    .modal-btn-close__icon {
        width: 20px;
        height: 20px;
    }

    .franchise-modal__title {
        font-size: 20px;
        line-height: 1.4;
        word-spacing: 0px;
        margin-bottom: 24px;
        width: 322px;
        text-align: center;
        align-self: center;
    }

    .franchise-modal__text {
        font-size: 16px;

        line-height: 1.4;
        margin-bottom: 30px;
        word-spacing: -2px;
    }

    .franchise-modal-form {
        gap: 16px;
        align-self: center;
    }

    .franchise-modal-form__input {
        width: 368px;
        height: 42px;

        &::placeholder {
            font-size: 14px;
        }
    }

    .franchise-modal-form__textarea {
        width: 368px;
        height: 84px;
        margin-bottom: 10px;

        &::placeholder {
            font-size: 14px;
        }
    }

    .franchise-modal-form__btn {
        width: 121px;
        font-size: 14px;
    }
}

@media screen and (max-width: 479px) {
    .franchise-modal {
        max-width: 100%;
        height: auto;
    }
}