.readmore-modal-backdrop {
    background-color: var(--backdrop-bcg-color);
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 100;
    transition: opacity 400ms cubic-bezier(0.4, 0, 0.2, 1),
        visibility 400ms cubic-bezier(0.4, 0, 0.2, 1);

    &.is-hidden {
            opacity: 0;
            visibility: hidden;
            pointer-events: none;
        }
}

.readmore-modal {
    display: flex;
    align-self: center;
    flex-direction: column;
    width: 280px;
    height: 459px;
    gap: 20px;
    background-color: var(--modal-bg-color);
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border: 2px solid var(--modal-border-color);
    border-radius: 10px;
    padding: 26px;
}

.readmore-modal__title {
    text-transform: capitalize;
    font-family: 'Seymour One', sans-serif;
    font-size: 16px;
    line-height: 1.5;
    color: var(--secondary-text-color);
    text-align: center;
}

 .modal-btn-close {
    cursor: pointer;
    background-color: transparent;
    padding: 0;
    position: absolute;
    top: 6px;
    right: 6px;
    border: none;
    }

.readmore-modal__video-box {
    display: flex;
    align-items: center;
    justify-content: center;

    width: 100%;
    height: 199px;

    background-color: var(--secondary-text-color);
    border-radius: 10px;
}

.readmore-modal__video {
    width: 224px;
    height: 199px;
}

.readmore-modal__play-icon {
    width: 36px;
    height: 36px;
}

.readmore-modal__text {
    letter-spacing: 0.02px;
    font-weight: 400;
    font-size: 14px;
    line-height: 140%;
    color: var(--secondary-text-color);
}

@media screen and (min-width: 720px) {
    .readmore-modal {
        padding: 30px 58px;
        gap: 20px;
        width: 568px;
        height: 449px;
    }

    .readmore-modal__text {
        font-size: 16px;
    }

    .readmore-modal__video {
        width: 448px;
        height: 205px;
    }
}