.location-backdrop {
    background-color: var(--backdrop-bcg-color);
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 100;
    transition: opacity 400ms cubic-bezier(0.4, 0, 0.2, 1),
        visibility 400ms cubic-bezier(0.4, 0, 0.2, 1);

    &.is-hidden {
        opacity: 0;
        visibility: hidden;
        pointer-events: none;
    }
}

.location-modal {
    width: 280px;
    height: 365px;
    gap: 20px;
    background-color: var(--modal-bg-color);
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border: 2px solid var(--modal-border-color);
    border-radius: 10px;
    padding: 28px;
    display: flex;
    align-self: center;
    flex-direction: column;
}

.modal-btn-close__icon {
    width: 16px;
    height: 16px;
}

.modal-btn-close {
    cursor: pointer;
    background-color: transparent;
    padding: 0;
    position: absolute;
    top: 6px;
    right: 6px;
    border: none;
}

.location-modal__text {
    text-transform: capitalize;
    font-family: 'Seymour One', sans-serif;
    font-size: 16px;
    line-height: 1.5;
    color: var(--secondary-text-color);
    text-align: center;
}

.location-modal__img {
    border-radius: 10px;
    width: 224px;
    height: 199px;
}

.location-modal__link {
    background-color: transparent;
    padding: 0;
    background-color: var(--default-btn);
    border-radius: 10px;
    width: 134px;
    height: 42px;
    font-weight: 600;
    font-size: 12px;
    line-height: 1.25;
    color: var(--secondary-text-color);
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    align-self: center;
    outline: none;
    position: relative;
    display: inline-block;
    overflow: hidden;
    transition: 0.2s;
    display: flex;
    text-decoration: none;

    &:hover {
        background-color: var(--hover-btn);
        color: var(--secondary-text-color);
        transition: 0.2s;
        transition-delay: 0.2s;

    }

    &:focus {
        background-color: var(--hover-btn);
        color: var(--primary-text-color);
        transition: 0.2s;
        transition-delay: 0.2s;
    }
}

.location-modal__line {
    position: absolute;
    display: block;
}

.location-modal__line-top {
    top: 0;
    left: -100%;
    width: 100%;
    height: 3px;
    background: linear-gradient(90deg, transparent, var(--hover-color));

    .location-modal__link:hover &,
    .location-modal__link:focus & {
        left: 100%;
        transition: 1s;
        transition-delay: 0.2s;
    }
}

.location-modal__line-bottom {
    bottom: 0;
    right: -100%;
    width: 100%;
    height: 3px;
    background: linear-gradient(270deg, transparent, var(--hover-color));

    .location-modal__link:hover &,
    .location-modal__link:focus & {
        right: 100%;
        transition: 1s;
        transition-delay: 0.2s;
    }
}

.location-modal__line-left {
    bottom: -100%;
    left: 0;
    width: 3px;
    height: 100%;
    background: linear-gradient(0deg, transparent, var(--hover-color));

    .location-modal__link:hover &,
    .location-modal__link:focus & {
        bottom: 100%;
        transition: 1s;
    }
}

.location-modal__line-right {
    top: -100%;
    right: 0;
    width: 3px;
    height: 100%;
    background: linear-gradient(180deg, transparent, var(--hover-color));

    .location-modal__link:hover &,
    .location-modal__link:focus & {
        top: 100%;
        transition: 1s;
    }
}

.location-modal__link-text {
    align-self: center;
}

@media screen and (min-width: 768px) {
    .location-modal {
        width: 568px;
        height: 458px;
        gap: 32px;
        padding: 32px 60px 32px 60px;
    }

    .location-modal__text {
        font-size: 20px;
        line-height: 1.4;
    }

    .modal-btn-close {
        top: 18px;
        right: 18px;
    }

    .modal-btn-close__icon {
        width: 20px;
        height: 20px;
    }

    .location-modal__img {
        width: 448px;
        height: 260px;
    }

    .location-modal__link {
        width: 145px;
        font-size: 14px;
        line-height: 1.21;
    }
}

@media screen and (max-width: 479px) {
    .location-modal {
        max-width: 100%;
    }
}